<style lang="less">
@import '../../styles/common.less';
@import 'components/table.less';
</style>

<template>
<div>
  <Row>
    <Col span="6">
    <Card>
      <p slot="title">
        <Icon type="ios-redo"></Icon>
        
      </p>
      <div class="edittable-test-con">
        <div id="showImage" class="margin-bottom-10">

          <Form ref="formItem" :model="formItem" :rules="ruleValidate" :label-width="80">
            <FormItem label=":" prop="computer_room">
              <Select v-model="formItem.computer_room" @on-change="Connection_Name">
              <Option v-for="i in datalist.computer_roomlist" :key="i" :value="i" >{{i}}</Option>
            </Select>
            </FormItem>

            <FormItem label=":" prop="connection_name">
              <Select v-model="formItem.connection_name" @on-change="DataBaseName" filterable>
              <Option v-for="i in datalist.connection_name_list" :value="i.connection_name" :key="i.connection_name">{{ i.connection_name }}</Option>
            </Select>
            </FormItem>

            <FormItem label=":" prop="basename">
              <Select v-model="formItem.basename" filterable>
              <Option v-for="item in datalist.basenamelist" :value="item" :key="item">{{ item }}</Option>
            </Select>
            </FormItem>

            <FormItem label=":" prop="text">
              <Input v-model="formItem.text" placeholder=""></Input>
            </FormItem>

            <FormItem label=":" prop="assigned">
              <Select v-model="formItem.assigned" filterable>
                <Option v-for="i in this.assigned" :value="i.username" :key="i.username">{{i.username}}</Option>
              </Select>
            </FormItem>

            <FormItem label="">
              <RadioGroup v-model="formItem.backup">
                <Radio label="1"></Radio>
                <Radio label="0"></Radio>
              </RadioGroup>
            </FormItem>
          </Form>
          <Form :label-width="30">
            <FormItem>
              <Button type="info" icon="paintbucket" @click.native="beautify()"></Button>
              <Button type="error" icon="trash-a" @click.native="ClearForm()" style="margin-left: 10%"></Button>
            </FormItem>

            <FormItem>
              <Button type="warning" icon="android-search" @click.native="test_sql()"></Button>
              <Button type="success" icon="ios-redo" @click.native="SubmitSQL()" style="margin-left: 10%" :disabled="this.validate_gen"></Button>
            </FormItem>
          </Form>

          <Alert style="height: 145px">
            
            <template slot="desc">
                <p>1. 0,1,2</p>
                <p>2. ,Audit completed</p>
                <p>3. </p>
                <p>4.sql</p>
                <p>:0</p>
              </template>
          </Alert>
        </div>
      </div>
    </Card>
    </Col>
    <Col span="18" class="padding-left-10">
    <Card>
      <p slot="title">
        <Icon type="ios-crop-strong"></Icon>
        sql
      </p>
      <editor v-model="formItem.textarea" @init="editorInit"></editor>
      <br>
      <br>
      <Table :columns="columnsName" :data="Testresults" highlight-row></Table>
    </Card>
    </Col>
  </Row>
</div>
</template>
<script>
import ICol from '../../../node_modules/iview/src/components/grid/col.vue'
import axios from 'axios'
import Cookies from 'js-cookie'
import util from '../../libs/util'
export default {
  components: {
    ICol,
    editor: require('../../libs/editor')
  },
  name: 'SQLsyntax',
  data () {
    return {
      validate_gen: true,
      formItem: {
        textarea: '',
        computer_room: '',
        connection_name: '',
        basename: '',
        text: '',
        backup: '0',
        assigned: ''
      },
      columnsName: [
        {
          title: 'ID',
          key: 'ID',
          width: '50'
        },
        {
          title: '',
          key: 'stage',
          width: '100'
        },
        {
          title: '',
          key: 'errlevel',
          width: '100'
        },
        {
          title: '',
          key: 'stagestatus',
          width: '150'
        },
        {
          title: '',
          key: 'errormessage'
        },
        {
          title: 'sql',
          key: 'sql'
        },
        {
          title: 'SQL',
          key: 'affected_rows',
          width: '130'
        }
      ],
      Testresults: [],
      item: {},
      datalist: {
        connection_name_list: [],
        basenamelist: [],
        sqllist: [],
        computer_roomlist: util.computer_room
      },
      ruleValidate: {
        computer_room: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        connection_name: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        basename: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        text: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            type: 'string',
            max: 20,
            message: '20',
            trigger: 'blur'
          }
        ],
        assigned: [{
          required: true,
          message: '',
          trigger: 'blur'
        }]
      },
      id: null,
      assigned: []
    }
  },
  methods: {
    editorInit: function () {
      require('brace/mode/mysql')
      require('brace/theme/xcode')
    },
    beautify () {
      axios.put(`${util.url}/sqlsyntax/beautify`, {
          'data': this.formItem.textarea
        })
        .then(res => {
          this.formItem.textarea = res.data
        })
        .catch(error => {
          this.$Notice.error({
            title: '',
            desc: error
          })
        })
    },
    Connection_Name (val) {
      this.datalist.connection_name_list = []
      this.datalist.basenamelist = []
      this.formItem.connection_name = ''
      this.formItem.basename = ''
      if (val) {
        this.ScreenConnection(val)
      }
    },
    ScreenConnection (val) {
      this.datalist.connection_name_list = this.item.filter(item => {
        if (item.computer_room === val) {
          return item
        }
      })
    },
    DataBaseName (index) {
      if (index) {
        this.id = this.item.filter(item => {
          if (item.connection_name === index) {
            return item
          }
        })
        axios.put(`${util.url}/workorder/basename`, {
            'id': this.id[0].id
          })
          .then(res => {
            this.datalist.basenamelist = res.data
          })
          .catch(() => {
            this.$Notice.error({
              title: '',
              desc: '!'
            })
          })
      }
    },
    test_sql () {
      let ddl = ['select', 'alter', 'drop', 'create']
      let createtable = this.formItem.textarea.replace(/(;|)$/gi, '').replace(/\s/g, ' ').replace(//g, ';').split(';')
      for (let i of createtable) {
        for (let c of ddl) {
          if (i.toLowerCase().indexOf(c) !== -1) {
            this.$Message.error('DML!');
            return false
          }
        }
      }
      this.$refs['formItem'].validate((valid) => {
        if (valid) {
          if (this.formItem.textarea) {
            let tmp = this.formItem.textarea.replace(/(;|)$/gi, '').replace(//g, ';')
            axios.put(`${util.url}/sqlsyntax/test`, {
                'id': this.id[0].id,
                'base': this.formItem.basename,
                'sql': tmp
              })
              .then(res => {
               if (res.data.status === 200) {
                 this.Testresults = res.data.result
                 let gen = 0
                 this.Testresults.forEach(vl => {
                   if (vl.errlevel !== 0) {
                     gen += 1
                   }
                 })
                 if (gen === 0) {
                   this.validate_gen = false
                 } else {
                   this.validate_gen = true
                 }
               } else {
                 this.$Notice.error({
                   title: '',
                   desc: 'Inception!'
                 })
               }
              })
              .catch(error => {
               util.ajanxerrorcode(this, error)
              })
          } else {
            this.$Message.error('sql!');
          }
        }
      })
    },
    SubmitSQL () {
      this.$refs['formItem'].validate((valid) => {
        if (valid) {
          if (this.formItem.textarea) {
            this.datalist.sqllist = this.formItem.textarea.replace(/(;|)$/gi, '').replace(/\s/g, ' ').replace(//g, ';').split(';')
            axios.post(`${util.url}/sqlsyntax/`, {
                'data': JSON.stringify(this.formItem),
                'sql': JSON.stringify(this.datalist.sqllist),
                'user': Cookies.get('user'),
                'type': 1,
                'id': this.id[0].id
              })
              .then(res => {
                this.$Notice.success({
                  title: '',
                  desc: res.data
                })
                this.ClearForm()
              })
              .catch(error => {
                util.ajanxerrorcode(this, error)
              })
          } else {
            this.$Message.error('sql!');
          }
          this.validate_gen = true
        } else {
          this.$Message.error('!');
        }
      })
    },
    ClearForm () {
      // this.$refs['formItem'].resetFields();
      this.formItem.textarea = ''
    }
  },
  mounted () {
    axios.put(`${util.url}/workorder/connection`, {'permissions_type': 'dml'})
      .then(res => {
        this.item = res.data['connection']
        this.assigned = res.data['person']
      })
      .catch(error => {
        util.ajanxerrorcode(this, error)
      })
    }
}
</script>



